import styled from 'styled-components';

export const NavBar = styled.nav`
  background: #001f3f !important;
  height: 80px;
  color: whitesmoke;
  opacity: 0.8;
`;

export const Title = styled.h2`
  margin: 0 auto;
  font-family: Impact;
  color: #FFDC00;
  letter-spacing: 2px;
`;



